.pricingSection {
    font-family: system-ui, Helvetica, sans-serif;
    color: gold;
    padding-left: 5vw;
    padding-right: 5vw;
    margin-top: 5vh;
}

.pricingHeader {
    font-family: fantasy;
}

.priceTableWrapper {
    overflow-x:auto;
    margin-top: 5vh;
}

.priceTable {
    border-collapse: collapse;
    width: 100%;
}

.priceTable td {
    padding: 1rem;
    border: 1px solid gold;
}

.priceTable tr {
    border: 1px solid gold;
}

.priceTable th {
    border: 1px solid gold;
}

.priceTableHeader {
    padding-bottom: 6vh;
}

.bestValueInfo {
    text-align: left;
}

.indent {
    padding-left: 1rem;
}

.numberList {
  margin-bottom: 1rem;
}

.goldReadingsArticle {
    margin-top: 12vh;
}
